/* Used to detect in JavaScript if apps have loaded styles or not. */
:root {
    --reach-dialog: 1;
}

[data-reach-dialog-overlay] {
    background: rgba(0, 0, 0, 0.33);
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: auto;
}

[data-reach-dialog-content] {
    width: 50vw;
    margin: 10vh auto;
    background: white;
    padding: 2rem;
    outline: none;
}

.ddb-btn {
    display: block;
    -webkit-appearance: none;
    border-radius: 5px;
    text-align: center;
    font-size: 16px;
    width: auto;
    max-width: 260px;
    padding: 20px 20px 14px 15px;
    box-sizing: border-box;
    line-height: 20px;
    cursor: pointer;
    transition: background 110ms cubic-bezier(0.2, 0, 0.38, 0.9);
}

@media (min-width: 672px) {
    .ddb-btn {
        width: 100%;
        padding: 20px 80px 14px 15px;
    }
}

.ddb-btn:focus {
    outline: 2px solid #808285;
    outline-offset: 0px;
}

.ddb-btn--align-left {
    text-align: left;
}

.ddb-btn--align-center {
    text-align: center;
    padding: 20px 20px 14px 15px;
}

.ddb-btn--align-right {
    text-align: right;
}

.ddb-btn--blank {
    width: auto;
    background: transparent;
    padding: 0;
}

.ddb-btn--blank:hover, .ddb-btn--blank:active {
    background: transparent;
}

.ddb-btn--grey {
    background: #e5e5e5;
    color: #474747;
}

.ddb-btn--grey:hover, .ddb-btn--grey:active {
    background: #808285;
    color: #fff;
}

.ddb-btn--black {
    background: #000;
    color: #fff;
}

.ddb-btn--black:hover {
    background: #808285;
    color: #fff;
}

.ddb-btn--secondary:hover {
    background: #f66d70;
    color: #fffff2;
}

.ddb-btn--charcoal {
    background: #262626;
    color: #fff;
}

.ddb-btn--charcoal:hover {
    background: #808285;
    color: #fff;
}

.ddb-btn__label {
    display: none;
}

@keyframes ddb-skeleton {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.4;
    }
    100% {
        opacity: 1;
    }
}

.ddb-skeleton {
    display: block;
    background-color: #ccc;
    animation: ddb-skeleton 700ms cubic-bezier(0.4, 0.14, 0.3, 1) infinite;
}

.ddb-skeleton-wrapper li {
    list-style: none;
}

.ddb-dialog__overlay {
    background: rgba(38, 38, 38, 0.2);
}

.ddb-dialog__content {
    background: #f1f2f2;
    border-radius: 5px;
    color: rgba(38, 38, 38, 0.8);
}

@keyframes ddb-dialog-dropdown {
    from {
        transform: translateY(-100%);
    }
    to {
        transform: translateY(0%);
    }
}

.ddb-dialog__content--dropdown {
    border-radius: 0px;
    box-sizing: border-box;
    width: 100%;
    margin: 0;
    min-height: 235px;
    padding: 16px 24px;
    transition: transform 70ms cubic-bezier(0, 0, 0.38, 0.9);
    animation: ddb-dialog-dropdown 240ms cubic-bezier(0, 0, 0.38, 0.9) forwards;
    box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.3);
}

.ddb-dialog__header {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 24px;
    box-sizing: content-box;
}

.ddb-dialog__close {
    padding: 16px;
    margin-right: -16px;
    margin-top: -16px;
}

.ddb-textfield__container {
    width: 100%;
}

.ddb-textfield__icon {
    display: block;
    margin-right: 16px;
    width: 20px;
    height: 18px;
    color: #e22045;
}

.ddb-textfield__action {
    display: flex;
    flex-direction: row-reverse;
}

.ddb-textfield__error {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: #e22045;
    height: 32px;
}

.ddb-textfield__input {
    background: #fff;
    width: 100%;
    box-sizing: border-box;
    display: block;
    border-radius: 5px;
    padding: 24px 16px 16px;
    font-size: 14px;
}

.ddb-textfield__input:focus {
    box-shadow: 0 0 8px #ccc;
}

.ddb-textfield__input--error, .ddb-textfield__input--error:focus {
    box-shadow: 0 0 0 2px #e22045;
}

.ddb-textfield__label {
    margin-bottom: 8px;
}

.ddb-textfield__label--hidden {
    display: none;
}

.ddb-icon--black {
    fill: #000;
}

.ddb-icon--grey {
    fill: #808285;
}

.ddb-icon--red {
    fill: #e22045;
}

.ddb-alert {
    display: block;
    height: 100%;
    padding: 16px;
    text-align: center;
    border-radius: 5px;
}

.ddb-alert--blank {
    display: block;
    height: auto;
    padding: 0;
    text-align: left;
    border-radius: 0;
}

.ddb-alert--info {
    background-color: #e5e5e5;
    color: #808285;
}

.ddb-alert--success {
    background-color: #24d273;
    color: #fff;
}

.ddb-alert--warning {
    background-color: #ffca5f;
    color: #fff;
}

.ddb-list-item {
    background: #f1f2f2;
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 16px;
}

@media (min-width: 672px) {
    .ddb-list-item {
        flex-direction: row;
        padding: 24px;
    }
}

@media (min-width: 1056px) {
    .ddb-list-item {
        padding: 32px;
    }
}

.ddb-list-item:nth-child(even) {
    background: #fff;
}

.ddb-list-item__children {
    display: flex;
    width: 100%;
    flex: 1;
}

.ddb-list-item__aside {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}

@media (min-width: 672px) {
    .ddb-list-item__aside {
        flex-direction: column;
        justify-content: flex-start;
    }
}

.ddb-simple-material {
    display: flex;
}

.ddb-simple-material__cover {
    display: flex;
    margin: 0;
    margin-right: 16px;
    width: 100px;
}

@media (min-width: 672px) {
    .ddb-simple-material__cover {
        margin-right: 24px;
    }
}

@media (min-width: 1056px) {
    .ddb-simple-material__cover {
        margin-right: 32px;
    }
}

.ddb-simple-material__cover a img {
    height: auto;
    width: 100%;
}

.ddb-simple-material__data {
    display: flex;
    flex-direction: column;
    max-width: 180px;
}

.ddb-simple-material__data a {
    text-decoration: none;
}

.ddb-simple-material__data p {
    margin: 0;
}

.ddb-simple-material__title {
    font-size: 16px;
    margin: 8px 0;
    color: #262626;
}

.ddb-simple-material__title:hover {
    color: #262626;
}

.ddb-simple-material__author-year {
    color: rgba(38, 38, 39, 0.5);
}

.ddb-simple-material-skeleton {
    display: flex;
}
